import { Button, Container, makeStyles, TextField, Typography } from "@material-ui/core"
import { useState } from "react"
import { useHistory } from 'react-router-dom'
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import { projectFireStore } from "../firebase/config";

const useStyles = makeStyles({
  feild: {
    marginTop: 20,
    marginBottom: 20,
    display: 'block'
  }
})

const Create = () => {

  const classes = useStyles()
  const history = useHistory()
  const [title,setTitle] = useState('')
  const [details,setDetails] = useState('')
  const [titleError,setTitleError] = useState(false)
  const [detailsError,setDetailsError] = useState(false)
  const [category,setCategory] = useState('money')

  const handleClick = e =>{
    e.preventDefault()
    setTitleError(false)
    setDetailsError(false)
    if(!title){
      setTitleError(true)
    }
    if(!details){
      setDetailsError(true)
    }
    if(title && details){

      // add to local host
      // fetch('http://localhost:8000/notes', {
      //   method: 'POST', // or 'PUT'
      //   headers: {
      //     'Content-Type': 'application/json',
      //   },
      //   body: JSON.stringify({ title, details, category, isLiked:"0"}),
      // })
      // .then(() => history.push('/'))

      projectFireStore.collection('notes').add({title, details, category, isLiked:"0"})
      history.push('/')
    }
  }
  

  return (
    <Container size="sm">
      <Typography
        variant="h4"
        color="textPrimary"
      >
        Create new note
      </Typography>
      <form noValidate onSubmit={handleClick}>
        <TextField
          onChange={ (e) => setTitle(e.target.value)}
          className={classes.feild}
          label="Title"
          variant="outlined"
          fullWidth
          required
          color="secondary"
          error={titleError}
        />
        <TextField
          onChange={ (e) => setDetails(e.target.value)}
          className={classes.feild}
          label="Details"
          variant="outlined"
          fullWidth
          required
          multiline
          rows={4}
          color="primary"
          error={detailsError}
        />
        <FormControl className={classes.feild}>
          <FormLabel >Category</FormLabel>
          <RadioGroup value={category} onChange={e => setCategory(e.target.value)}>
            <FormControlLabel color="primary" value="money" control={<Radio color="primary"/>} label="Money" />
            <FormControlLabel value="reminder" control={<Radio color="primary"/>} label="Reminder" />
            <FormControlLabel value="college" control={<Radio color="primary"/>} label="College" />
            <FormControlLabel value="mentorship"control={<Radio color="primary"/>} label="Mentorship" />
          </RadioGroup>
        </FormControl>
        <Button type="Submit" variant="contained" color="primary" >
          Submit
        </Button>
      </form>
    </Container>
  );
}

export default Create;