import { Avatar, Card, CardContent, CardHeader, Container, Grid, makeStyles, Typography } from "@material-ui/core";
import { blue, blueGrey, brown, deepOrange, deepPurple } from "@material-ui/core/colors";
import { useEffect, useState } from "react";
import Masonry from 'react-masonry-css'
import { projectFireStore } from "../firebase/config";

const useStyles = makeStyles({
  orange: {
    backgroundColor: deepOrange[500],
  },
  purple: {
    backgroundColor: deepPurple[500],
  },
  blue: {
    backgroundColor: blue[500],
  },
  brown: {
    backgroundColor: brown[500],
  },
  blueGrey: {
    backgroundColor: blueGrey[500],
  }
});

const Favourites = () => {

  const classes = useStyles()

  //const [notes, setNotes] = useState([])

  // get data from localhost
  // useEffect( () => {
  //   fetch('http://localhost:8000/notes')
  //     .then( res => res.json())
  //     .then( data => setNotes(data))
  // }, [])

  const useFirestoreFavourites = (collection) => {

    const [ blogs, setBlogs ] = useState([]);
    const [ isPending, setIsPending ] = useState(true)
    const [ noBlogs, setNoBlogs] = useState("")
  
    useEffect(() => {
      const unsub = projectFireStore.collection(collection)
        .where("isLiked","==","1")
        .onSnapshot( snap => {
          if (snap.empty) {
            setNoBlogs("No Blogs")
            setIsPending(false)
          }
          let documents = []
          snap.forEach( doc => {
            documents.push({...doc.data(), id: doc.id })
            setIsPending(false)
          });
          setBlogs(documents)
        })
        return () => unsub()
    },[collection])
    return { blogs, isPending, noBlogs }
  }

  const { blogs, isPending, noBlogs } = useFirestoreFavourites("notes")

  const avatarStyle = e =>{
    switch (e) {
      case "money":
        return classes.orange
      case "college":
        return classes.purple
      case "mentorship":
        return classes.blue
      case "reminder":
        return classes.brown
      default:
        return classes.blueGrey
    }
  }

  const breakPoints = {
    default: 3,
    1000: 2,
    700: 1
  }

  return (
    <Container>
      { isPending && <Typography variant="h6">Loading...</Typography>}
      { noBlogs && <Typography variant="h4">There is no Favourite notes 💔</Typography>}
      { blogs &&
        // <Grid spacing={3} container>
          <Masonry
            breakpointCols={breakPoints}
            className="my-masonry-grid"
            columnClassName="my-masonry-grid_column"
          >
            {blogs.map( note => (
            <div key={note.id}>
            <Card >
              <CardHeader
                avatar={
                  <Avatar className={avatarStyle(note.category)} >
                    {note.category.charAt(0).toUpperCase()}
                  </Avatar>
                }
                title={note.title}
                subheader={note.category}
              />
                <CardContent>
                  <Typography variant="body2" color="textSecondary" component="p">{note.details}</Typography>
                </CardContent>
              </Card>
            </div>
            ))}
          </Masonry>
        // </Grid>
      }
    </Container>
  );
}

export default Favourites;
