import { AppBar, Divider, Drawer, List, ListItem, ListItemIcon, ListItemText, makeStyles, Toolbar, Typography } from "@material-ui/core";
import NoteOutlinedIcon from '@material-ui/icons/NoteOutlined';
import NoteAddOutlinedIcon from '@material-ui/icons/NoteAddOutlined';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import GradeOutlinedIcon from '@material-ui/icons/GradeOutlined';
import { useHistory } from "react-router-dom";


const drawerWidth = 240

const useStyles = makeStyles( (theme) => {
  return(
    {page: {
      background: '#fff',
      width: '100%',
      height: '100%',
      paddingTop: '20px'
    },
    drawer: {
      width: drawerWidth,
      backgroundColor: '#f9f9f9'
    },
    drawerPaper: {
      width: drawerWidth,
      backgroundColor: '#f9f9f9'
    },
    title: {
      padding: '5px 10px'
    },
    root: {
      display: 'flex',
    },
    appBar: {
      backgroundColor: '#f9f9f9',
      width: `calc(100% - ${drawerWidth}px)`,
      color: '#333'
    },
    toolBar : theme.mixins.toolbar,
  }
  )
})

const Layout = ({children}) => {

  const classes = useStyles()

  const pages = [
    { name: "Notes", icon: <NoteOutlinedIcon color='primary'/> , path: "/"},
    { name: "Create a note", icon: <NoteAddOutlinedIcon color='primary'/> , path: "/create"},
    { name: "Favourites", icon: <GradeOutlinedIcon color='primary'/> , path: "/favourites"}
  ]

  const history = useHistory()

  return (
    <div className={classes.root}>
      {/* app bar */}
      <AppBar className={classes.appBar} elevation={2}>
        <Toolbar>
          <Typography variant="h6">
            Notes App
          </Typography>
        </Toolbar>
      </AppBar>

      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="left"
      >
        <Typography
          variant="h4"
          className={classes.title}
        >
          Note App
        </Typography>
        <Divider />
        <List>
          { pages.map( page => (
            <ListItem button key={page.name} onClick={() => history.push(page.path)}>
              <ListItemIcon>{page.icon}</ListItemIcon>
              <ListItemText primary={page.name} />
            </ListItem>
          ))}
        </List>
        <Divider />
        <List>
          <ListItem button onClick={() => history.push("/about")} >
            <ListItemIcon> <InfoOutlinedIcon color='primary' /></ListItemIcon>
            <ListItemText primary='About' />
          </ListItem>
        </List>
      </Drawer>

      <div className={classes.page}>
        <div className={classes.toolBar}></div>
        {children}
      </div>

    </div>
  );
}

export default Layout;