import { Avatar, Card, CardContent, CardHeader, IconButton, makeStyles, Typography } from '@material-ui/core';
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import FavoriteBorderOutlinedIcon from '@material-ui/icons/FavoriteBorderOutlined';
import FavoriteOutlinedIcon from '@material-ui/icons/FavoriteOutlined';
import { deepOrange, deepPurple, blue, brown, blueGrey } from '@material-ui/core/colors';
import { useState } from 'react';
import { projectFireStore } from '../firebase/config';


const useStyles = makeStyles({
  orange: {
    backgroundColor: deepOrange[500],
  },
  purple: {
    backgroundColor: deepPurple[500],
  },
  blue: {
    backgroundColor: blue[500],
  },
  brown: {
    backgroundColor: brown[500],
  },
  blueGrey: {
    backgroundColor: blueGrey[500],
  }
});


const NoteCard = ({note, handleDelete}) => {

  const classes = useStyles()

  const [ isLikedx, setIsLiked ] = useState(note.isLiked === "1")

  // liked localhost
  // const handleLiked = (id , isLiked) => {

  //   if(isLikedx){               // note is liked
  //     fetch('http://localhost:8000/notes/'+id, {
  //       method: "PATCH",
  //       headers: {
  //           "Content-Type" : "application/json"
  //         },
  //       body: JSON.stringify(
  //         {
  //           "isLiked": "0"
  //         }
  //       )
  //     })
  //     setIsLiked(false)
  //   }
  //   else {                          // note isn't liked
  //     fetch('http://localhost:8000/notes/'+id, {
  //       method: "PATCH",
  //       headers: {
  //           "Content-Type" : "application/json"
  //         },
  //       body: JSON.stringify(
  //         {
  //           "isLiked": "1"
  //         }
  //       )
  //     })
  //     setIsLiked(true)
  //   }

  // }

  const handleLiked = (id , isLiked) => {

    var washingtonRef = projectFireStore.collection('notes').doc(id)

    if(isLikedx){               // note is liked

      // Set the "isLiked" field to '0'
      setIsLiked(false)
      return washingtonRef.update({
        "isLiked": "0"
      })
      
    }
    else {                          // note isn't liked

      setIsLiked(true)
      return washingtonRef.update({
        "isLiked": "1"
      })
    }

  }

  const avatarStyle = e =>{
    switch (e) {
      case "money":
        return classes.orange
      case "college":
        return classes.purple
      case "mentorship":
        return classes.blue
      case "reminder":
        return classes.brown
      default:
        return classes.blueGrey
    }
  }

  return (
    <Card >
      <CardHeader
        avatar={
          <Avatar className={avatarStyle(note.category)} >
            {note.category.charAt(0).toUpperCase()}
          </Avatar>
        }
        action={
          <div>
            {/* delete */}
            <IconButton aria-label="settings" onClick={() => handleDelete(note.id)}>
              <DeleteForeverOutlinedIcon />
            </IconButton>
            {/* like */}
            <IconButton aria-label="settings" onClick={() => handleLiked(note.id , note.isLiked)}>
              {isLikedx? <FavoriteOutlinedIcon /> :<FavoriteBorderOutlinedIcon />}
            </IconButton>
          </div>
        }
        title={note.title}
        subheader={note.category}
      />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">{note.details}</Typography>
      </CardContent>
    </Card>
  );
}

export default NoteCard;